/** @format */

import React from "react";
import classes from "./NoInstagram.module.css";
import group from "../../../../assets/icons-2/Group 43.jpg";

import RightArrow from "../../../../assets/icons/rightArrow.svg";

const NoInstagram = ({ setReload = (f) => f, reload = undefined, pageId }) => {
  return (
    <div className={classes.Type0_Account}>
      <div className={classes.Type0_Note}>
        <p>We could not find any connected instagram account</p>
      </div>
      <main className={classes.Type0_Link}>
        <p>Connect your instagram with your facebook page</p>
        <img src={group} alt="connect" />
        <button
          className={classes.Type0_Button}
          onClick={() =>
            window.open(
              `https://www.facebook.com/${pageId}/settings/?tab=instagram_management`,
              "_blank"
            )
          }
        >
          Link Manually
        </button>

        <p className={classes.skipBtn}>
          Skip Instagram
          <img src={RightArrow} alt="skip" />
        </p>
      </main>
      <p className={classes.Done_Linking}>
        Done linking?{" "}
        <span onClick={() => setReload(!reload)}>Resume Setup!</span>
      </p>
    </div>
  );
};

export default NoInstagram;
